.App {
  font-family: sans-serif;
  /* text-align: center; */
}
body {
  background-color: #f0f0f07c;
}
.product {
  position: absolute;
  text-align: center;
  justify-content: center;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}
.product_card {
  height: 500px;
  width: 300px;
  background-color: white;
  border-radius: 12px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.product_card img {
  width: 250px;
  border-radius: 10px;
  top: 15px;
}
